
.switcher_container {
    display: block;    
    width: 60px;
    height: 30px;
    background-color: white;
    border-radius: 80px;
}

.dark{
    margin: 5px 0px 0px 2px;
    vertical-align: middle;
    display: inline-block;
    height: 20px;
    width: 20px;
    background-color: rgb(14, 14, 14);
    border-radius: 80px;
}
.light{
    margin: 5px 0px 0px 38px;
    vertical-align: middle;
    display: inline-block;
    height: 20px;
    width: 20px;
    background-color: rgb(14, 14, 14);
    border-radius: 80px;
    
}
