.card_container {
    display: flex;
    flex-direction: column;
    padding: 5px;
    overflow : auto;
    height: calc(100vh - 4rem);
}
.controls {
    width: 100%;
    display: flex;
    flex-direction: row;
    
    justify-content: flex-start;
}
.card_container h1 {
    color: orangered;
}
.calculated_result{
    padding: 10px 0px;
    line-height: 2;
    text-align: left;
}
.controls img {
    padding: 0px 10px;
    width: 30px;
    height: 30px;
}
.card_container button {    
    margin: 10px 0px;
    background-color: black;
    border: 1px solid white;
    border-radius: 5px;
    color: white;
    height: 40px;
    font-size: 15px;
}


input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }
  /* Firefox */
input[type=number] {
    -moz-appearance: textfield;
  }
.card_container input {
    -webkit-appearance: none;
    background-color: black;
    border: 1px solid white;
    border-radius: 5px;
    color: white;
    height: 30px;
    font-size: 15px;
}