.home_page_container{
    display: flex;
    flex-direction: column;
    
    
    padding: 5px;
}
.home_page_info{
    padding: 0px 10px 0px 50px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: row;
    border-bottom-right-radius: 5px;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    border-bottom-left-radius: 100px;
    /* background-color:rgb(5, 81, 136);  */
    background: linear-gradient(45deg, rgb(55, 55, 125), rgb(149, 7, 133));
}
.home_page_info h1 {
    font-size: xx-large;
}
.home_page_info h1 span {
    font-size: 25px;
}
.company_logo {
    border-radius: 10px;
    width: 100px;
    height: 100px;
}
.main_page_navigation{
    height: calc(70vh - 2rem);
    margin: 0;
    overflow-y: scroll;
    padding: 0%;
}
.home_page_container ul{
    padding: 10px 0px;
    overflow-y: scroll;
    display: grid;
    grid-gap: 2px;
    grid-template-columns: repeat(2,1fr);
    grid-gap: 20px;
    grid-auto-rows: 1fr;
    margin:0;

}
.home_page_container ul li {
    height: 180px;
    padding: 5px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    border-radius: 20px;
    color: black;
    opacity: 0.9;
    transition: all 0.3s ease-in-out;
    &:hover {
      opacity: 1;
    }
 
}

.home_page_container ul li span{
    font-size:larger;
    color: rgb(255, 255, 255);
    font-weight: 600;
    align-items: center;
    justify-self: flex-end;
}

.home_page_container ul img {
    width: 30px;
    height: 30px;
    /* padding-right: 10px; */
    display: inline-block;
    align-self: center;
    justify-self:flex-end;
    vertical-align:text-bottom;
}

